import type { SerializedStyles, Theme } from '@emotion/react';
import { css } from '@emotion/react';

import { useDesignSystemTheme } from '../Hooks';
import { useDialogComboboxContext } from './hooks/useDialogComboboxContext';

const getButtonContainerStyles = (theme: Theme): SerializedStyles => {
  return css({
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'flex-end',
    padding: `${theme.spacing.sm}px ${theme.spacing.lg / 2}px ${theme.spacing.sm}px ${theme.spacing.lg / 2}px`,
    gap: theme.spacing.sm,
    alignSelf: 'stretch',
  });
};

export const DialogComboboxButtonContainer = ({ children, ...restProps }: React.HTMLAttributes<HTMLDivElement>) => {
  const { theme } = useDesignSystemTheme();
  const { isInsideDialogCombobox } = useDialogComboboxContext();

  if (!isInsideDialogCombobox) {
    throw new Error('`DialogComboboxButtonContainer` must be used within `DialogCombobox`');
  }

  return (
    <div {...restProps} css={getButtonContainerStyles(theme)}>
      {children}
    </div>
  );
};
